*,
*::before,
*::after {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell,
    'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

img {
  display: block;
  max-width: 100%;
}

$color: #49b8e5, #1e98d4, #2a92d0, #3a88c8, #507cbe;

.search-form {
  display: flex;
  justify-content: center;
  padding: 40px;
}

.input__search-form {
  display: block;
  height: calc(2.25rem + 2px);
  padding: 0.375rem 0.75rem;
  font-family: inherit;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #bdbdbd;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}

.input__search-form:focus {
  border-color: hsl(245, 100%, 42%);
  box-shadow: 0 0 0 3px hsla(245, 100%, calc(42% + 40%), 0.8);
  outline: 3px solid transparent;
}

.btn-submit {
  font-family: inherit;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-clip: padding-box;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  cursor: pointer;
  margin-left: 20px;
  background-color: slateblue;
  border: 1px solid slateblue;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    background-color: aqua;
    border: 1px solid aqua;
    outline: aqua;
  }
}

.btn__load-more {
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-clip: padding-box;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
  cursor: pointer;
  margin-left: auto;
  margin-right: auto;
  display: block;
  background-color: slateblue;
  border: 1px solid slateblue;
  padding: 8px 8px;
  border-radius: 6px;
  margin-bottom: 40px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover {
    background-color: aqua;
    border: 1px solid aqua;
  }
  &.is-hidden {
    display: none;
  }
}

.gallery {
  margin-top: 60px;
  margin-bottom: 120px;
  display: grid;
  max-width: 1200px;
  grid-template-columns: 350px repeat(auto-fill, 350px) 350px 350px;
  grid-auto-rows: 200px;
  grid-gap: 90px 10px;
  justify-content: center;
  padding: 0;
  list-style: none;
  margin-left: auto;
  margin-right: auto;
}

.photo-card {
  // box-shadow: 0px 1px 3px 0px rgb(0 0 0 / 20%), 0px 1px 1px 0px rgb(0 0 0 / 14%),
  //   0px 2px 1px -1px rgb(0 0 0 / 12%);
  position: relative;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover {
    transform: scale(1.03);
  }
}

.img-gallery {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.info {
  display: flex;
  border: 1px solid #eeeeee;
  border-top: none;
  padding-left: 10px;
  padding-right: 10px;
}

.shrink {
  flex-shrink: 4;
}

.photo-ref {
  display: block;
  text-decoration: none;
  height: 100%;
}

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 80px;
  overflow: hidden;
  &.is-hidden {
    display: none;
  }
}

$circle-size: 20px;
$circle-margin: 7px;

.circle {
  width: $circle-size;
  height: $circle-size;
  border-radius: 50%;
  margin: $circle-margin;
  display: flex;
  justify-content: center;
  align-items: center;

  &:before {
    content: '';
    width: $circle-size;
    height: $circle-size;
    border-radius: 50%;
    opacity: 0.7;
    animation: scale 2s infinite cubic-bezier(0, 0, 0.49, 1.02);
  }
}

@for $i from 1 through 5 {
  .circle-#{$i} {
    background-color: nth($color, $i);
    &:before {
      background-color: nth($color, $i);
      animation-delay: 200ms * $i;
    }
  }
}

@keyframes scale {
  0% {
    transform: scale(1);
  }
  50%,
  75% {
    transform: scale(2.5);
  }
  78%,
  100% {
    opacity: 0;
  }
}
